import React from 'react'
import Select from "react-select"
import "./CustomDropdownStyle.css"
import api from '../api.js';
import { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { setProductsErrorMessage, setProductsLoading, setSearchedProducts, setSelectedPropertyValue } from '../state/propertiesSearch/propertiesSearchSlice';
function CustomDropdown(props) {
  const dispatch = useDispatch()
  const selectedProperty = useSelector((state)=>state.propertiesSearch.selectedProperty)
  const selectedPropertyValue = useSelector((state)=>state.propertiesSearch.selectedPropertyValue)
  async function handleChange(value){
   try{
     dispatch(setSelectedPropertyValue(value))
     dispatch(setSearchedProducts([]))
     dispatch(setProductsLoading(true))
     dispatch(setProductsErrorMessage(""))
     const res = await api.post("search/get_products", {
      "property" : selectedProperty,
      "property_val": value["value"]
     })
     dispatch(setProductsLoading(false))
     dispatch(setSearchedProducts(res.data["results"]))
   }catch(e){
    dispatch(setProductsLoading(false))
    dispatch(setProductsErrorMessage(String(e)))
   }
  }
  return (
    <div className='unique-value-select'><Select options={props.results}
    value={selectedPropertyValue}
    onChange={handleChange}
    /></div>
  )
}
export default CustomDropdown