import { createSlice } from "@reduxjs/toolkit";
const initialState = {
    searchString : "",
    isSearchPropertiesLoading: false,
    searchResultsList: [],
    searchPropertiesErrorMessage: "",
    selectedSubCategory: "",
    selectedProperty: "",
    showResults: true,
    uniquePropertyValues: [],
    uniqueValuesLoading: false,
    uniqueValuesErrorMessage: "",
    selectedPropertyValue: "",
    searchedProducts: [],
    productsLoading: false,
    productsErrorMessage: "",
    selectedProduct: {},
}

const propertiesSearchSlice = createSlice({
    name: "propertiesSearch",
    initialState,
    reducers:{
        setSearchString: (state, action)=>{
            state.searchString = action.payload;
        },
        setIsSearchPropertiesLoading: (state, action)=>{
            state.isSearchPropertiesLoading = action.payload;
        },
        setSearchResultsList: (state, action)=>{
            state.searchResultsList = action.payload;
        },
        setSearchPropertiesErrorMessage: (state, action)=>{
            state.searchPropertiesErrorMessage = action.payload;
        },
        setSelectedSubCategory: (state, action)=>{
            state.selectedSubCategory = action.payload;
        },
        setSelectedProperty: (state, action)=>{
            state.selectedProperty = action.payload;
        },
        setShowResults: (state, action)=>{
            state.showResults = action.payload;
        },
        setUniquePropertyValues: (state, action)=>{
            state.uniquePropertyValues = action.payload;
        },
        setUniqueValuesLoading: (state, action)=>{
            state.uniqueValuesLoading = action.payload;
        },
        setUniqueValuesErrorMessage: (state, action)=>{
            state.uniqueValuesErrorMessage = action.payload;
        },
        setSelectedPropertyValue: (state, action)=> {
            state.selectedPropertyValue = action.payload;
        },
        setSearchedProducts: (state, action)=> {
            state.searchedProducts =  action.payload;
        },
        setProductsLoading: (state, action)=> {
            state.productsLoading =  action.payload;
        },
        setProductsErrorMessage: (state, action)=> {
           state.productsErrorMessage = action.payload;
        },
        setSelectedProduct: (state, action)=> {
            state.selectedProduct = action.payload;
        }

    }
});

export const {setSearchString, setIsSearchPropertiesLoading,setSearchResultsList, setSearchPropertiesErrorMessage, setSelectedSubCategory, setSelectedProperty, setShowResults, setUniquePropertyValues, setUniqueValuesLoading, setUniqueValuesErrorMessage, setSelectedPropertyValue, setSearchedProducts, setProductsLoading, setProductsErrorMessage, setSelectedProduct} =  propertiesSearchSlice.actions;
export default propertiesSearchSlice.reducer;