import React from 'react'
import "../ProductDetailsPageStyle.css"
import { useSelector } from 'react-redux'
function ProductDetailsPage() {
  const selectedProduct = useSelector((state)=>state.propertiesSearch.selectedProduct)
  return (
    <div className='product-details-div'>
      <h1>Machine of ID <span style={{color:"red"}}><b>{selectedProduct["id"]}</b></span></h1>
      
      <table>
        <thead>
          <th>Key</th>
          <th>Value</th>
        </thead>
        {

          Object.keys(selectedProduct).map((property)=>{
            return <tr key={property}>
            <td>{property}</td>
            <td>{selectedProduct[property]}</td>
            </tr>
          })
       
        }
      </table>
      {
        
      }
    </div>
  )
}

export default ProductDetailsPage