import React from 'react'
import "./ProductResultStyle.css"
import '@fortawesome/fontawesome-free/css/all.min.css';
import { useDispatch } from 'react-redux';
import { setSelectedProduct } from '../state/propertiesSearch/propertiesSearchSlice';
import { useNavigate } from 'react-router-dom';

function ProductResult(props) {
  const dispatch = useDispatch()
  const navigate = useNavigate()
  function onPress(){
    dispatch(setSelectedProduct(props.product))
    navigate('/product_details')
  }
  return (
    <div onClick={onPress} className='product-item-div'>
      <i style={{fontSize:"x-large"}} class="fa-solid fa-gear"></i>
      <h2>{"Machine " + props.product["id"]}</h2>
    </div>
  )
}

export default ProductResult