import React, { use } from 'react'
import "../SearchPageStyle.css"
import SearchBar from '../SearchBar'
import { useSelector } from 'react-redux'
import loadingIndicator from "../../images/loading.gif";
import SearchResultList from '../SearchResultList';
import ProductResultsList from '../ProductResultsList';

import Alert from '@mui/material/Alert';
import CustomDropdown from '../CustomDropdown';

function SearchPage() {
  const isLoading = useSelector((state)=>state.propertiesSearch.isSearchPropertiesLoading)
  const errorMessage = useSelector((state)=>state.propertiesSearch.searchPropertiesErrorMessage)
  const searchResults = useSelector((state)=>state.propertiesSearch.searchResultsList)
  const searchString = useSelector((state)=>state.propertiesSearch.searchString)
  const subcategory = useSelector((state)=> state.propertiesSearch.selectedSubCategory)
  const showResults = useSelector((state)=> state.propertiesSearch.showResults)
  const uniqueValuesLoading = useSelector((state)=>state.propertiesSearch.uniqueValuesLoading)
  const uniqueValuesError = useSelector((state)=>state.propertiesSearch.uniqueValuesErrorMessage)
  const uniqueValueResults = useSelector((state)=>state.propertiesSearch.uniquePropertyValues)
  const selectedProperty = useSelector((state)=>state.propertiesSearch.selectedProperty)
  const productsLoading = useSelector((state)=>state.propertiesSearch.productsLoading)
  const productsErrorMessage = useSelector((state)=>state.propertiesSearch.productsErrorMessage)
  const searchedProducts = useSelector((state)=>state.propertiesSearch.searchedProducts)
  const selectedPropertyValue = useSelector((state)=>state.propertiesSearch.selectedPropertyValue)
  return (
    <div className='search-page-container'>
      <h1>Searching in <span style={{color:"red"}}><b>{subcategory}</b></span></h1>
        <SearchBar/>
        {isLoading == true? <img style={{marginTop:"1%"}} src={loadingIndicator}/>: errorMessage == ""? (searchResults.length > 0 && showResults? <SearchResultList/>: searchString != "" && selectedProperty == ""? <h2>No Results Found</h2>: <></>): <Alert style={{marginTop:"1%"}} severity='error'>{errorMessage}</Alert>}
        { uniqueValuesLoading == true? <img style={{marginTop:"1%"}} src={loadingIndicator}/>: uniqueValuesError != ""? <Alert style={{marginTop:"1%"}} severity='error'>{uniqueValuesError}</Alert>:uniqueValueResults.length > 0? <CustomDropdown results  = {uniqueValueResults}/>: <></>}
        { productsLoading == true? <img style={{marginTop:"6%"}} src={loadingIndicator}/>: productsErrorMessage != ""? <Alert style={{marginTop:"6%"}} severity='error'>{productsErrorMessage}</Alert>:searchedProducts.length > 0? <ProductResultsList products = {searchedProducts}/> : selectedPropertyValue != ""?  <h2 style={{marginTop:"1%"}}>No Results Found</h2>: <></>}

    </div>
  )
}

export default SearchPage