import React from 'react'
import ProductResult from './ProductResult'
import "./ProductResultsListStyle.css"
function ProductResultsList(props) {
  return (
    <div className='products-list-div'>{props.products.map((product, id)=>{
        return <ProductResult key = {id} product = {product}/>
    })}</div>
  )
}

export default ProductResultsList